"use client"

import React, { createContext, useContext, useEffect, useState, ReactNode } from 'react'
import { io, Socket } from 'socket.io-client'

interface WebsiteSettings {
  website_name: string
  tagline: string
  logo_url: string | null
  favicon_url: string | null
  currency: string
  timezone: string
  email: string
  phone: string
  address: string
  refund_policy: string
  return_policy: string
  terms_conditions: string
}

interface RealtimeContextType {
  settings: WebsiteSettings
  updateSettings: (newSettings: Partial<WebsiteSettings>) => void
  isConnected: boolean
}

const defaultSettings: WebsiteSettings = {
  website_name: "Syed Asad Raza",
  tagline: "Your Premier Multi-Vendor Marketplace",
  logo_url: null,
  favicon_url: null,
  currency: "USD",
  timezone: "UTC",
  email: "admin@sar.com",
  phone: "+1 234 567 8900",
  address: "123 Business Street, City, Country",
  refund_policy: "",
  return_policy: "",
  terms_conditions: ""
}

const RealtimeContext = createContext<RealtimeContextType | undefined>(undefined)

export const useRealtime = () => {
  const context = useContext(RealtimeContext)
  if (!context) {
    throw new Error('useRealtime must be used within a RealtimeProvider')
  }
  return context
}

interface RealtimeProviderProps {
  children: ReactNode
}

export const RealtimeProvider: React.FC<RealtimeProviderProps> = ({ children }) => {
  const [settings, setSettings] = useState<WebsiteSettings>(defaultSettings)
  const [socket, setSocket] = useState<Socket | null>(null)
  const [isConnected, setIsConnected] = useState(false)

  useEffect(() => {
    // Initialize socket connection
    const socketInstance = io({
      path: '/api/socket',
      addTrailingSlash: false,
    })

    socketInstance.on('connect', () => {
      console.log('Connected to socket server')
      setIsConnected(true)
    })

    socketInstance.on('disconnect', () => {
      console.log('Disconnected from socket server')
      setIsConnected(false)
    })

    socketInstance.on('settings-update', (newSettings: WebsiteSettings) => {
      console.log('Received settings update:', newSettings)
      setSettings(newSettings)
    })

    setSocket(socketInstance)

    // Cleanup on unmount
    return () => {
      socketInstance.disconnect()
    }
  }, [])

  const updateSettings = (newSettings: Partial<WebsiteSettings>) => {
    if (socket) {
      socket.emit('admin-settings-change', newSettings)
    }
  }

  const value: RealtimeContextType = {
    settings,
    updateSettings,
    isConnected
  }

  return (
    <RealtimeContext.Provider value={value}>
      {children}
    </RealtimeContext.Provider>
  )
}
